import {Router, Route, Routes} from "react-router-dom";
import Company from "./routes/company";
import React from "react";
import Home from "./routes/home";
import Product from "./routes/product"
import Gallery from "./routes/gallery";
import KMap from "./routes/kakao";
import CMap from "./routes/cmap";
import Productr from "./routes/product_r";


function App () {
    return(
      <Router basename="/InKwang">
        <Routes>
          <Route path="/InKwang/" element={<Home />} /> {/* Home 경로 */}
          <Route path="/InKwang/company" element={<Company />} />
          <Route path="/InKwang/product" element={<Product />} />
          <Route path="/InKwang/product_r" element={<Productr />} />
          <Route path="/InKwang/gallery" element={<Gallery />} />
          <Route path="/InKwang/kakaomap" element={<KMap />} />
          <Route path="/InKwang/cmap" element={<CMap />} />
        </Routes>
      </Router>

    );
}

export default App;
